      h1 Toma de muestras
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0
        p El objetivo del muestreo es obtener una parte representativa del material bajo estudio (cuerpo de agua, efluente industrial, agua residual, suelos, emisiones, etc.) para la cual se analizarn las variables fisicoqumicas y microbiolgicas de inters, para esto se requiere que el estudiante identifique la importancia de elaborar el plan de muestreo y protocolos de muestreo especficos segn factor ambiental evaluado.
        .cajon.color-acento-contenido.p-4
          p.mb-0 En algunos casos, el objetivo del muestreo es demostrar que se cumplen las normas especificadas por la legislacin ambiental como actividades de prevencin y control ambiental y otros casos como medida de correccin y vigilancia cuando se ha ocasionado un impacto ambiental significativo como la contaminacin ambiental en un ecosistema.
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img1.png', style="width: 450px" alt="").m-auto
    .row.justify-content-center.mb-5
      .col-lg-10
        .row.bg-c1
          .col-12.px-4.px-lg-2.pt-4.pt-lg-2.pb-4.pb-lg-0
            .row.align-items-center
              .col-12.col-lg-auto.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img1.svg', style="width: 80px"  alt="").m-auto
              .col
                p.mb-0 La bsqueda principal del proceso de muestreo es lograr informacin relacionada con la concentracin de las sustancias contaminantes o constitutivas de los elementos analizados (agua, suelo o aire). Con ello, establecer probables medidas de proteccin de la salud humana y de los diversos ecosistemas.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Toma de muestras de agua y suelo: pasos y normativa
    .bloque-texto-g.bloque-texto-g--inverso.color-acento-contenido.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img2.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Aunque probablemente no sea para siempre, en la actualidad es altamente probable que las personas disfruten, o hayan disfrutado, de un entorno natural apto, con algn ro, lago, mar, aguas cristalinas, con la posibilidad de respirar sin exponer la vida, la salud o la integridad.
          br
          br
          | De otra parte y, tal vez, con menor frecuencia, los seres humanos habitan, ocupan o transitan lugares de aguas terrosas con imposibilidad de ver a travs de ellas, y donde los olores son no solo insoportables, sino que generan afectacin a la seguridad y al bienestar biolgico, por los altos y medios niveles de contaminacin.
    p.mb-5 En relacin con los pasos y normativa para los procesos de muestreo, tenga presente:
    .row.justify-content-center
      .col-lg-5.mb-4.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema1/img2.svg'  alt="")
          .tarjeta.color-primario
            .text-white.p-4
              h4.text-center Importancia
              p La importancia del muestreo y del transporte y recepcin de muestras radica en que la informacin sobre sustancias que alteran la calidad del medio ha de ser significativa y fiable, de manera que la toma de decisiones sobre el sitio, potencialmente alterado o contaminado, sea adecuada a su particular problemtica.
      .col-lg-5
        .tarjeta-avatar
          img(src='@/assets/curso/tema1/img3.svg'  alt="")
          .tarjeta.color-primario
            .text-white.p-4
              h4.text-center La normativa
              p Desde lo relacionado con la normativa, en Colombia, para los procesos de toma de muestras, se adopta como referencia la Norma Tcnica Colombiana del ICONTEC: tcnicas de muestreo, toma de muestras, preservacin y manejo para el componente agua, suelo, aire.
    Separador
    #t_1_1
    .titulo-segundo.color-secundario
      h2 1.1 Conceptos bsicos de unidades de medida

    figure.my-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/au50DCVNjDc" title="Sistemas de Unidades" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video. Sistemas de Unidades

    p.mb-5 Un sistema de unidades especifica las unidades de las cantidades fundamentales de longitud, masa, tiempo y fuerza. El sistema internacional de unidades es la referencia estndar para unidades de medida a nivel mundial; este cuenta con siete unidades bases para las magnitudes fundamentales como se muestra en la siguiente tabla:

    .row.justify-content-center
      .col-lg-10.mb-4.mb-lg-0
        .titulo-sexto.color-acento-contenido
          p.mb-0 #[b Tabla 1.] Unidades bases para magnitudes
        .tabla-b.color-acento-contenido.mb-5
          table
            tr.bg-c2
              th.text-center Magnitud
