              td.text-center Mol


    p Los prefijos del sistema internacional de unidades, son representados por smbolos que se anteponen a la unidad, y representan un factor por el que debe ser multiplicada la cantidad.
    p.mb-5 En la siguiente tabla se presentan algunos de los prefijos ms usados:
    .row.justify-content-center
      .col-lg-10
        .titulo-sexto.color-acento-contenido
          p.mb-0 #[b Tabla 2.] Prefijos ms usados en campo de estudio de muestras
        .tabla-b.color-acento-contenido.mb-5
          table
            tr.bg-c2
              th.text-center Prefijo
              th.text-center Smbolo
              th.text-center Factor
              th.text-center Equivalente
            tr
              td.text-center Giga
