    .row.justify-content-center.mb-5
      .col-lg-8.bg-c1.p-4
        p Los prefijos se emplean para facilitar la expresin de cantidades.
        .row.justify-content-center
          .col-lg-8.bg-c3.p-4
            p #[b Ejemplo 1: 11500m = 11,5*10<sup>3</sup>m = 11,5 km]
            p.mb-0 #[b Ejemplo 2: 0,00528g = 5,28*10<sup>-3</sup>g = 5,28 mg]

    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Uso de otras unidades
    .row.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0
        p Existen otras unidades que se usan con bastante frecuencia y que, aun sin hacer parte del Sistema Internacional de Unidades, es posible emplearlas en conjunto. Entre estas se encuentran:
        .row
          .col-12.bg-c3.p-4
            .row.justify-content-center
              .col-lg-8
                ul.lista-ul--color.lista-ul.mb-0
                  li
                    i.fas.fa-check
                    span #[b El minuto (m): 1m = 60s]
                  li
                    i.fas.fa-check
                    span #[b La hora (h): 1h = 3600s ]
                  li
                    i.fas.fa-check
                    span #[b El da (d): 1d = 24h = 86400s]
                  li
                    i.fas.fa-check
                    span #[b El litro (L,l): 1L = 1dm#[sup 3] = 10#[sup -3]m#[sup 3]]
                  li
                    i.fas.fa-check
                    span #[b La tonelada (t): 1t = 10#[sup 3]kg]
      .col-lg-4
        figure
          img(src='@/assets/curso/tema1/img4.svg', style="width: 450px"  alt="").m-auto

    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Unidades consistentes

    .row.justify-content-center
      .col-lg-8
        figure.mb-4
          img(src="@/assets/curso/tema1/img-unidades-consistentes.svg", alt="Imagen que presenta el simbolo y abreviatura de algunas unidades de medida.").m-auto
    p El desarrollo de los protocolos y procedimientos en este y en diferentes campos de estudio, involucra el uso de ecuaciones, cuyos elementos y resultados deben ser expresados empleando las unidades correctas. Por lo que, si las unidades en la ecuacin no son consistentes, las respuestas sern errneas.
    p.mb-5 Por lo anterior, para realizar procedimientos de cancelacin de unidades, se deben seguir los siguientes pasos:
    .row.mb-5
      .col-lg-12.bg-c4.p-4.p-lg-5
        ol.lista-ol--cuadro.lista-ol.mb-0
          li
            .lista-ol--cuadro__vineta
              span.txt-c2 1.
            span #[b Despejar de la ecuacin el trmino que se desea]
          li
            .lista-ol--cuadro__vineta
              span.txt-c2 2.
            span #[b Definir las unidades que se requieren para el resultado]
          li
            .lista-ol--cuadro__vineta
              span.txt-c2 3.
            span #[b Sustituir en la ecuacin los valores con sus respectivas unidades]
          li
            .lista-ol--cuadro__vineta
              span.txt-c2 4.
            span #[b Cancelar las unidades de los trminos que parezcan en el numerador y el denominador]
          li
            .lista-ol--cuadro__vineta
              span.txt-c2 5.
            span #[b Utilizar los factores de conversin para eliminar las unidades no deseadas, y obtener las que se definieron previamente en el paso dos]
          li.mb-0
            .lista-ol--cuadro__vineta
              span.txt-c2 6.
            span #[b Realizar el clculo]
    p.mb-5 Ample su conocimiento sobre las unidades de medida, analizando en detalle el siguiente ejemplo:
    .row.justify-content-center
      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Situacin
        p.mb-4 Para conocer el tiempo que tardar en llenarse un tanque de 1000 L, al usar una manguera por la que fluye un caudal de 0.4L/s.
        figure
          img(src='@/assets/curso/tema1/img6.svg', style="width: 80px"  alt="").m-auto
      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Ecuacin empleada
        p.mb-4 Se usa la ecuacin que aqu se muestra, donde Q es caudal, v es volumen y t es tiempo.
        .bg-c3.my-3
          .p-3
            p.text-center.mb-0 #[strong Q = V / t]

      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Despeje de tiempo
        p.mb-4 Enseguida se deber despejar tiempo en la ecuacin que aqu se muestra.
        .bg-c3.my-3
          .p-3
            p.text-center.mb-0 #[strong t = V / Q]

      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Unidades de resultado
        p.mb-0 Se proponen las unidades del resultado; para este caso pueden ser minutos, ya que el ejercicio no especifica las unidades finales.
        p.mb-4 Se sustituye t en la ecuacin.
        figure
          img(src='@/assets/curso/tema1/img9.svg', style="width: 280px"  alt="Imagen que representa la frmula t=V/Q= 1000L/0.4L/s").m-auto
    .row.justify-content-center
      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Simplificacin
        p.mb-4 Simplificando la fraccin se obtiene.
        figure
          img(src='@/assets/curso/tema1/img10.svg', style="width: 240px"  alt="Imagen que representa la frmula t = (1000L/1) / (0.4l/s) = (1000 L*s) / (0.4s)").m-auto
      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Cancelacin de unidades
        p.mb-4 Se cancelan las unidades y aplicamos los factores de conversin.
        figure
          img(src='@/assets/curso/tema1/img11.svg', style="width: 180px"  alt="Imagen que representa la frmula t=(1000 L*s) / 0.4L").m-auto
      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Factores de conversin
        p.mb-4 Se aplican los factores de conversin (y cancelacin de unidades).
        figure
          img(src='@/assets/curso/tema1/img12.svg', style="width: 240px"  alt="Imagen que representa la frmula t = (1000s/0.4) * (1 min/60s)").m-auto
      .col-10.col-lg.bg-c5.p-4.m-2
        .h4.text-center Resultado
        p.mb-4 Se realiza el clculo para obtener el resultado.
        figure
          img(src='@/assets/curso/tema1/img13.svg', style="width: 280px"  alt="Imagen que representa la frmula t=(1000s/0.4) * (1min/60s) = 41.67min").m-auto
    Separador
    #t_1_2
    .titulo-segundo.color-secundario
      h2 1.2 Caractersticas bsicas de rotulado y envasado
    .row.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0
        p La informacin sobre las operaciones de muestreo es una parte fundamental para asegurar la calidad del programa de muestreo y base fundamental en la cadena de custodia de las muestras, ya que esto genera confianza en los resultados de los anlisis aplicados a las muestras. Por tal razn, la informacin consignada en los rtulos de los recipientes y en los registros de toma de muestras debe ser legible, veraz y trazable.
        p Los rtulos de las muestras pueden:
        .row
          .col-12.bg-c6.p-4
            ul.lista-ul--color.lista-ul.mb-0
              li
                i.fas.fa-check
                span #[b Ser adhesivos:] Tener forma de etiquetas adhesivas, de marca (rtulo) o ambas.
              li
                i.fas.fa-check
                span #[b Ser removibles:] Ser removibles para hacer parte del proceso de registros posteriores.
              li
                i.fas.fa-check
                span #[b Ser desvirtuados:] Enfrentarse a la posibilidad de remocin inapropiada o prdida.
              li
                i.fas.fa-check
                span #[b Ser econmicos y prcticos:] Estar hechos, normalmente, en papel a prueba de agua con tinta indeleble y son pegados al frasco o recipiente de muestra.
              li
                i.fas.fa-check
                span #[b Usarse funcionalmente:] Estar escritos directamente en el recipiente con tinta indeleble, sin embargo, no es lo ms aconsejado.
              li
                i.fas.fa-check
                span #[b Usarse con oportunidad:] Deben ser diligenciados justo antes o inmediatamente despus de la toma de muestras.
              li
                i.fas.fa-check
                span #[b Ajustarse al recipiente:] Deben fijarse en el recipiente de tal forma que se asegure que no se borrar o perder la informacin.
              li
                i.fas.fa-check
                span #[b Ofrecer claridad:] Deben ser totalmente legibles (no realizar tachones).
      .col-lg-4
        figure
          img(src='@/assets/curso/tema1/img3.png', style="width: 340px"  alt="").m-auto
    .row.justify-content-center.mb-5
      .col-lg-10
        .row.bg-c1
          .col-12.px-4.px-lg-2.pt-4.pt-lg-2.pb-4.pb-lg-0
            .row.align-items-center
              .col-12.col-lg-auto.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img1.svg', style="width: 80px"  alt="").m-auto
              .col
                p.mb-0 La informacin mnima que debe contener un rtulo es: cdigo del proyecto, consideraciones de seguridad y cdigo del cronograma, nombre de identificacin de la muestra, identificacin de la ubicacin del muestreo, punto de muestreo, intervalo de tiempo (Fecha y hora), tipo de muestreo, empresa o persona que realiza la toma, parmetros medidos #[em in situ].
    .row.justify-content-center.mb-5
      .col-lg-10
        .row.bg-c1
          .col-12.px-4.px-lg-2.pt-4.pt-lg-2.pb-4.pb-lg-0
            .row.align-items-center
              .col-12.col-lg-auto.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img1.svg', style="width: 80px"  alt="").m-auto
              .col
                p.mb-0 Para muestras microbiolgicas se debe complementar la informacin con la preservacin realizada (se debe consignar en el momento del alistamiento de los recipientes en el laboratorio, ya que la adicin del preservante se hace antes del proceso de esterilizacin de los frascos).
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Envasado del muestreo

    .row.align-items-center.mb-5
      .col-auto
        figure
          img(src='@/assets/curso/tema1/img-envasado.svg', alt="").m-auto
      .col
        p.mb-0 Segn los anlisis que vayan a realizarse se definir el tipo de envase a utilizar. El mismo estar en funcin de la cantidad de muestra a tomar y de la necesidad de dejar (en anlisis microbiolgicos) o no (en la mayora de los anlisis) una cmara de aire o un espacio para mezclas o para el agregado de algn reactivo que permita la conservacin de la muestra. Este aspecto debe ser tenido en cuenta tanto al planificar el muestreo como al realizar la toma de la muestra.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Envases para muestreo de agua
    p.mb-5 Las muestras de agua pueden ser tomadas para anlisis fisicoqumico o tambin para anlisis microbiolgico. En cada caso, hay unos criterios de envasado que deben seguirse:
    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido
          p.mb-0 #[b Tabla 3.] Envases para muestreo de agua
        .tabla-b.color-acento-contenido.mb-5
          table
            tr.bg-c2
              th.text-center Para anlisis fisicoqumico
              th.text-center Para anlisis microbiolgico
            tr
              td #[b 1.] Se utilizarn envases de plstico o vidrio, con buen cierre, nuevos. Si se pensara en reutilizar un envase, deben desestimarse envases de gaseosas u otras bebidas, o que hayan contenido agua contaminada, soluciones concentradas, etc., nicamente podran reutilizarse envases de agua mineral.
              td #[b A.] Se utilizarn envases con capacidad de 250-300ml, de plstico o vidrio, esterilizados, con tapa hermtica y en lo posible de boca ancha (frascos).
            tr
              td
                p #[b 2.] En ambos casos debe asegurarse que el envase se encuentre LIMPIO, pero debe prestarse especial atencin a no lavarlo con detergentes, hipoclorito de sodio (lavandina) u otros reactivos: el envase solo puede ser enjuagado con agua.
                p.mb-0 De todas maneras, se trate de un envase nuevo o reutilizado, previo a la toma de la muestra, deber enjuagarse por lo menos tres veces con el agua a muestrear.
              td #[b B.] Tambin pueden utilizarse bolsas especiales de polietileno estriles (fabricadas a tal fin), considerando que este tipo de envase es muy cmodo para la recoleccin y cerrado.
            tr
              td #[b 3.] La cantidad de muestra necesaria para un anlisis fsico-qumico es de aproximadamente 1000ml. Si fuera necesario muestrear para algn anlisis que requiriera del agregado de un reactivo especfico para la conservacin de la muestra, deber preverse la toma en envases adicionales de menor capacidad.
              td #[b C.] Es muy importante tener presente al seleccionar los envases, que este tipo de muestras debe mantenerse refrigerada hasta su llegada al laboratorio y procesamiento.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Envases para muestreo de suelo
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0
        p Una vez son tomadas las muestras de suelo, estas son rotuladas y almacenadas en recipientes plsticos y de vidrio. Para toma de muestras de suelo no contaminadas se pueden usar recipientes de polietileno (como cubetas, botellas de boca ancha, talegas fuertes) porque son inertes, baratos y cmodos.
        .row
          .col-12.bg-c7.p-4.p-lg-5
            p.mb-0 Para toma de muestras de suelo con sospecha que estn contaminados es esencial asegurarse de que el material del recipiente de la muestra sea tal que la muestra permanezca siendo representativa. Para este tipo no se recomienda recipientes de plstico en caso de que exista contaminacin orgnica como pesticidas, o talegas de polietileno no son adecuadas para suelos contaminados.
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img4.png', style="width: 470px"  alt="").m-auto
    .row.justify-content-center.mb-5
      .col-lg-10
        .row.bg-c1
          .col-12.px-4.px-lg-2.pt-4.pt-lg-2.pb-4.pb-lg-0
            .row.align-items-center
              .col-12.col-lg-auto.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img1.svg', style="width: 80px"  alt="").m-auto
              .col
                p.mb-0 El recipiente se debe sellar en forma tal que no haya perdida de componentes voltiles tales como humedad o solventes. Los recipientes con muestras se deben llenar y sellar de forma que haya un mnimo espacio con aire.
    p.mb-5 Del mismo modo que con las muestras de agua, las muestras de suelo tendrn un anlisis fisicoqumico o microbiolgico. En cada caso, hay unos criterios de envasado que deben seguirse:
    .row.align-items-center.mb-5
      .col-lg-5.pe-0
        figure
          img(src='@/assets/curso/tema1/img5.png', style="width: 510px"  alt="").m-auto
      .col-lg-7.bg-c3.p-4
        p #[b Para anlisis fsico-qumico]
        p.mb-0 Se utilizarn envases de plstico de polietileno rgido o flexible (bolsas plsticas) o vidrio, con buen cierre, nuevos de acuerdo al tipo de anlisis. Debe asegurarse que el envase se encuentre LIMPIO, pero debe prestarse especial atencin a no lavarlo con detergentes, hipoclorito de sodio (lavandina) u otros reactivos: el envase solo puede ser enjuagado con agua.
    .row.align-items-center.mb-5
      .col-lg-5.pe-0
        figure
