        p.mb-0 Se utilizarn envases de plstico o vidrio, esterilizados, con tapa hermtica y en lo posible de boca ancha (frascos). Tambin pueden utilizarse bolsas especiales de polietileno estriles (fabricadas a tal fin), considerando que este tipo de envase es muy cmodo para la recoleccin y cerrado. Es muy importante tener presente al seleccionar los envases, que este tipo de muestras debe mantenerse refrigerada hasta su llegada al laboratorio y procesamiento.
