    #t_1_3
    .titulo-segundo.color-secundario
      h2 1.3 Capacitacin del personal y seguridad y salud en el trabajo
    p.mb-5 Uno de los ejes estratgicos para realizar un proceso de toma y tratamiento de muestras acorde a la normatividad es el recurso humano que realizar esa funcin, el cual debe contar con las habilidades y competencias necesarias; estas mismas son definidas dentro de un instrumento de planeacin denominado plan de capacitacin.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Formacin y evaluacin
    .row.mb-5
      .col-lg-2.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img15.svg', style="width: 250px"  alt="").m-auto
      .col-lg-10
        p La capacitacin del personal de toma de muestras es tan importante que cuenta con un marco normativo que lo exige, como son: las resoluciones 1073 de 2003 y  1570 de 2004 del MAVDT, en donde se estipula que este personal debe ser formado, evaluado y certificado, como mnimo, en las Normas de Competencia Laboral cdigo 280201034 #[b Realizar los procedimientos de muestreo del agua de acuerdo con los protocolos de la entidad]; cdigo 280201001 #[b Asegurar las condiciones de salud y seguridad en el puesto de trabajo] y cdigo 280201002 #[b Generar informacin para apoyar la toma de decisiones empresariales].
        p La capacitacin del personal de toma de muestras en suelos tambin requiere capacitacin en el tema de muestreo en suelos, aunque no existe, todava, una norma de competencia laboral que obligue.
    .row.bg-c8.mb-5
      .col-12
        .p-5
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p Para cualquiera de los dos personales de toma de muestra sea en agua o suelo, es importante que toda organizacin defina un plan de capacitacin, en temas como:
            .col-lg-5
              ul.lista-ul--color.lista-ul.mb-0
                li
                  i.fas.fa-check
                  span #[b Elementos personales de proteccin para la toma y procesamiento de muestras.]
                li
                  i.fas.fa-check
                  span #[b Equipos e instrumentos de laboratorio.]
                li
                  i.fas.fa-check
                  span #[b Riesgos y amenazas en campo y en laboratorio.]
                li
                  i.fas.fa-check
                  span #[b Tipos, tcnicas y mtodos de muestreo.]
                li
                  i.fas.fa-check
                  span #[b Normatividad aplicable, entre otros.]
    .row.justify-content-center.mb-5
      .col-lg-10
        .row.bg-c1
          .col-12.px-4.px-lg-2.pt-4.pt-lg-2.pb-4.pb-lg-0
            .row.align-items-center
              .col-12.col-lg-auto.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img1.svg', style="width: 80px"  alt="").m-auto
              .col
                p.mb-0 Estar capacitados y permanentemente formados, no solamente va a significar una mejora en la calidad y expectativas laborales, sino que se van a minimizar los errores involucrados en el proceso de toma, preservacin y transporte de muestras.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Sobre la seguridad y salud en el trabajo de muestreo
    .row.mb-4
      .col-lg-8.mb-4.mb-lg-0
        p De acuerdo a la Sociedad americana de qumica (2003), en su publicacin seguridad en los laboratorios qumicos acadmicos, prevenir accidentes de cualquier tipo en un laboratorio, es responsabilidad de todos los que usan este espacio de trabajo, por lo cual es necesario tener siempre una actitud cooperativa activa.
        p Los accidentes que se pueden dar cuando se trabaja con insumos qumicos bien sea dentro de un laboratorio o en pruebas de campo, suelen ser asociadas a:
        .row
          .col-12.bg-c3.p-4
            .row.justify-content-center
              .col-10.col-lg-8
                ul.lista-ul--color.lista-ul.mb-0
                  li
                    i.fas.fa-check
                    span #[b Actividades de indiferencia.]
                  li
                    i.fas.fa-check
                    span #[b No utilizar el sentido comn.]
                  li.mb-0
                    i.fas.fa-check
                    span #[b No seguir instrucciones y cometer errores.]
      .col-lg-4
        figure
          img(src='@/assets/curso/tema1/img16.svg', style="width: 450px").m-auto
    p.mb-4 Cuando se menciona tomar un Rol activo se est haciendo referencia a seguir, siempre, recomendaciones como:
    .row.mb-4
      .col-12.bg-c3.p-4
        .row.justify-content-center
          .col-10.col-lg-8
            ul.lista-ul--color.lista-ul.mb-0
              li
                i.fas.fa-check
                | #[b Seguir las reglas de seguridad.]
              li
                i.fas.fa-check
                | #[b No jugar bromas mientras realiza actividades de toma de muestras o de laboratorio.]
              li
                i.fas.fa-check
                | #[b Familiarizarse con los equipos de seguridad.]
              li
                i.fas.fa-check
                | #[b Siempre indagar antes sobre los peligrosos si va a manejar sustancias qumicas.]
    p.mb-5 Para realizar la actividad de toma de muestras se debe garantizar que las personas cuenten con unos implementos de seguridad mnimos, como:
    .tarjeta.tarjeta--azul.p-4.mb-5
      SlyderA(tipo="b")
        .row.m-1
          .col-12.p-4.p-lg-5
            h4 Casco
            p.mb-5 En campo es necesario como prevencin.
            figure.mb-0
              img(src='@/assets/curso/tema1/img7.png', style="width: 500px"  alt="Imagen de un casco amarillo").m-auto
        .row.m-1
          .col-12.p-4.p-lg-5
            h4 Cofia
            p.mb-5 Si tiene el cabello largo, mantngalo recogido durante el desarrollo de la prctica. Use la cofia si el lder del proceso lo considera necesario.
            figure.mb-0
              img(src='@/assets/curso/tema1/img8.png', style="width: 500px"  alt="Imagen de una cofia azul").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Proteccin de ojos
            p Es obligatorio que todo el personal que permanezca en el laboratorio use permanentemente las gafas de seguridad, aun cuando no estn realizando prcticas. Las personas que utilizan gafas de manera permanente deben disponer de unas gafas de seguridad suplementarias, No utilice lentes de contacto dentro del laboratorio, ya que su uso facilita la acumulacin de sustancias txicas en los ojos. Los lentes blandos absorben los vapores de sustancias txicas, ocasionando la irritacin, adems de interferir con los procedimientos de lavado de emergencia.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img9.png', style="width: 500px"  alt="Imagen de unas gafas de proteccin").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Mascarilla o Tapabocas
            p Cuando la prctica de laboratorio implique la manipulacin de compuestos voltiles, txicos, nocivos y/o irritantes, es indispensable emplear equipos de proteccin respiratoria, adaptados y homologados para la sustancia en cuestin. Tambin deben utilizarse en caso de fugas y derrames de las sustancias anteriores, dada la gran concentracin ambiental que resulta de los mismos.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img10.png', style="width: 500px"  alt="Tapabocas").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Instrumental de campo o vestimenta (Guardapolvo blanco)
            p Primero se debe aclarar que la ropa utilizada en laboratorio o en campo, debe proteger tanto de salpicaduras como de derrames, preferiblemente un guardapolvo blanco, si es para uno de cuencas de agua contaminadas este debe ser impermeable.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img11.png', style="width: 500px"  alt="").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Bata de laboratorio
            p Si se usa la bata las caractersticas ms aconsejables son: Material: algodn, dril o gabardina, Color: blanco, Manga: larga con puo resortado, Largo (extensin): mnimo hasta el muslo (3/4), Tipo de cierre: de abotonar a presin.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img12.png', style="width: 500px"  alt="").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Zapatos
            p En el caso de los laboratorios se debe usar zapatos totalmente cerrados y que no sean de cuero o cuero sinttico, no se podr usar sandalias o cualquier zapato que deje piel al descubierto ni con tacones.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img13.png', style="width: 500px"  alt="").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Guantes
            p De nitrilo, de neopreno, gruesos y delgados. Cualquier manipulacin de sustancias corrosivas, irritantes, de elevada toxicidad o de elevado poder de penetracin a travs de la piel, debe ser llevada a cabo empleando guantes adecuados y limpios. Finalizada la prctica, lave los guantes con abundante agua y jabn. Haga lo propio con sus manos.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img14.png', style="width: 500px"  alt="").m-auto
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Peligros qumicos
    .row
      .col-lg-7.mb-4.mb-lg-0
        p Las sustancias qumicas pueden causar dao si no son manipuladas de forma adecuada y segn protocolos, lo ms importante por resaltar es que estas sustancias tienen diferentes caractersticas (txicas, inflamables, corrosivas y reactivas) y debe reconocerlas y saber cmo reaccionar en caso de accidentes.
        .row.justify-content-center
          .col-12
            .row.bg-c1
              .col-12.px-4.px-lg-4.pt-4.pt-lg-2.pb-4.pb-lg-0
                .row.align-items-center
                  .col-12.col-lg-auto.mb-4.mb-lg-0
                    figure
                      img(src='@/assets/curso/tema1/img1.svg', style="width: 80px"  alt="").m-auto
                  .col
                    p.mb-0 Existen sustancias que cuentan con ms de un peligro, por lo cual se considera que el grado de peligrosidad de las sustancias qumicas puede variar, por ello siempre trabajar de forma segura es la manera correcta de desarrollar actividades con estos implementos.
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img15.png', style="width: 450px"  alt="").m-auto
