    #t_1_4
    .titulo-segundo.color-secundario
      h2 1.4 Plan de muestreo y tcnicas de muestreo
    .row.align-items-end.mb-4
      .col-lg-7.mb-4.mb-lg-0
        p El plan de muestreo es un instrumento de planeacin en el cual se deja todo consolidado sobre el muestreo a realizar. En general se recomienda que el plan de muestreo especifique como mnimo los siguientes tems:
        .row
          .col-12.bg-c7.p-4
            ul.lista-ul--color.lista-ul.mb-0
              li
                i.fas.fa-check
                span #[b El objetivo.]
              li
                i.fas.fa-check
                span #[b El sitio de toma de muestra.]
              li
                i.fas.fa-check
                span #[b El mtodo y tcnica a usar.]
              li
                i.fas.fa-check
                span #[b Los parmetros a medir.]
              li
                i.fas.fa-check
                span #[b Los instrumentos y equipos requeridos.]
              li
                i.fas.fa-check
                span #[b El personal y las competencias que debe manejar.]
              li
                i.fas.fa-check
                span #[b Los elementos de seguridad y salud en el trabajo.]
              li.mb-0
                i.fas.fa-check
                span #[b Los formatos requeridos para el registro de informacin.]
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img16.png', style="width: 500px", alt="").m-auto

    p Antes de iniciar el muestreo, es elemental tener claramente definido la forma como sern tomadas las muestras para poder realizar un trabajo de campo. Dentro de los tipos de muestras se pueden encontrar: muestras sencillas, compuestas, peridicas, continuas y de serie.
    p.mb-5 A continuacin, profundice en las generalidades y particularidades de los tipos de muestras:
    .row.mb-5
      .col-12.bg-c9.p-4.p-lg-5
        p.mb-4 #[b Plan de muestreo, tipos de muestras]
        LineaTiempoE.color-acento-contenido
          .row(titulo="Muestras simples" subtitulo="")
            .col-12
              p.mb-0 Este tipo de muestra se toma en un momento y lugar determinado. Es aplicable en los casos que se sabe, la composicin del agua no vara significativamente en el tiempo o lugar, como lo son usualmente para caracterizacin fisicoqumica de aguas naturales. Este tipo de muestra simple se usa en el muestreo de grifos.
          .row(titulo="Muestras compuestas" subtitulo="")
            .col-12
              p.mb-0 Aquellas que son usadas, en su mayora, para vertimientos domsticos e industriales. Debido a la variacin horaria de su caudal son muy utilizadas en el monitoreo de ros, vertimientos o procesos industriales en lnea. Su toma se realiza en un mismo punto en intervalos de tiempo previamente definidos y el volumen de cada muestra parcial puede ser constante o variar en funcin del caudal.
          .row(titulo="Cuando hay variacin del caudal" subtitulo="")
            .col-12
              p.mb-4 Para los casos en que se requiere variar el volumen en cada toma, se recolectan muestras parciales (alcuotas). Ejemplo: cada 2 o 3 horas, cuyo #[strong volumen] se obtiene segn la siguiente relacin:
              .row.justify-content-center
                .col-lg-5.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img18.svg', style="width: 270px"  alt="Imagen que representa la frmula Vp = Qp * (Vc / N * Q)").m-auto
                .col-lg-6
                  .bg-c13.my-2.my-lg-0
                    .p-3
                      p.mb-1.text-small #[strong V#[sub p]]: Volumen de cada muestra parcial.
                      p.mb-1.text-small #[strong V#[sub c]]: Volumen de muestra compuesta.
                      p.mb-1.text-small #[strong Q#[sub p]]: Caudal parcial del agua.
                      p.mb-1.text-small #[strong Q]: Caudal promedio.
                      p.mb-1.text-small #[strong N]: Nmero de muestras parciales.

          .row(titulo="Estudios de calidad ambiental marina" subtitulo="")
            .col-12
              p.mb-0 En estos casos es necesario el monitoreo de afluentes con el fin de conocer las descargas y el volumen de contaminantes que llegan al mar, por tal motivo es necesario realizar muestreos compuestos para lograr la correcta caracterizacin de dichos afluentes (que pueden ser ros o descargas de aguas residuales). Es importante resaltar que este tipo de muestra genera resultados de concentraciones promedio.
          .row(titulo="Muestras integradas" subtitulo="")
            .col-12
              p.mb-0 Este tipo de muestra consiste en la recoleccin de muestras simples en diferentes puntos de manera simultnea que posteriormente son mezcladas. En caso de presentarse variaciones de caudal en los puntos de muestreo, se debe tener en cuenta para que el volumen aportado sea proporcional al caudal medido durante la toma de la muestra.
          .row(titulo="Muestras en serie" subtitulo="")
            .col-12
              p.mb-0 Dentro de las muestras en serie se distinguen dos tipos, de perfil profundo y de rea: las primeras son muy aplicables en oceanografa cuando el objetivo es conocer la variacin vertical de un parmetro, por ejemplo, definir la posicin de la capa termohalina, de la picnoclina, etc. Las segundas, son una serie de muestras de agua tomadas a una profundidad en particular, de una masa de agua en diversas locaciones; muy utilizadas para definir distribuciones espaciales por capas.
    p El proceso de toma, preservacin y transporte de muestras, supone el establecimiento previo de las tcnicas de muestreo que se debern aplicar para obtener resultados satisfactorios.
    p.mb-5 Dentro de las principales tcnicas de muestreo, se pueden mencionar y explicar las siguientes:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--verde")
      .row(titulo="Muestreo manual")
        .col-12
          p.mb-0 El muestreo manual se realiza cuando se tienen sitios de fcil acceso o aquellos que por medio de ciertas adaptaciones puedan facilitar la toma de muestras. La ventaja de este tipo de muestreo es permitir al encargado de tomar la   muestra, observar los cambios en las caractersticas del agua en cuanto a sustancias flotantes, color, olor, aumento o disminucin de caudales, entre otros; sin embargo, este solo es aceptable para los criterios de control y vigilancia, si la muestra es representativa de la calidad del agua del sitio de muestreo particular, motivo por el cual se requiere establecer que la informacin obtenida de estas muestras puntuales tomadas en un sitio y tiempo dados es nica para ese lugar y tiempo seleccionado.
      .row(titulo="Muestreo automtico")
        .col-12
          p.mb-0 Para aquellos lugares de difcil acceso se recomiendan los muestreadores automticos, ya que cuentan con ms precisin en la toma de la muestra, aun cuando su mayor desventaja sea el montaje y calibracin del equipo, pues su aplicacin implica la instalacin de equipos (antenas, paneles solares, etc.) y herramientas (licencias de trasmisin, software) que elevan el costo, convirtindose en un factor limitante para la implementacin de este tipo de muestreo. Entre los equipos automticos ms representativos para determinar la calidad del agua destinada para consumo humano, se destacan los: muestreadores secuenciales automticos para proveer muestras compuestas en un perodo extenso de tiempo, Sistemas de monitoreo en lnea (sistema SAMOS para deteccin de pesticidas, y nuevos sensores), Sistemas biolgicos de alarma temprana que alerten si hay contaminacin en la muestra y muestreadores pasivos, as llamados porque simulan captacin biolgica.
      .row(titulo="Muestreo mixto")
        .col-12
          p.mb-0 Se pueden poner en marcha programas de muestreo que involucren la utilizacin de los dos tipos de muestreo mencionados anteriormente (muestreo mixto), convirtiendo el monitoreo en un sistema integrado que permite la verificacin manual de los resultados obtenidos de forma automtica. Dicha verificacin es realizada aleatoriamente, de tal manera que se pueda realizar la calibracin, ajuste y mantenimiento de los equipos automticos. Las tcnicas del muestreo de agua para anlisis microbiolgicos bsicamente incluyen procedimientos para prevenir su contaminacin durante el llenado de los recipientes por microorganismos presentes en el exterior de los grifos, accesorios, en el ambiente o en el operario. Las tcnicas de llenado de los recipientes se pueden dividir en dos: muestreo en el grifo o muestreo por inmersin.
      .row(titulo="Muestreo por inmersin")
        .col-12
          p.mb-0 El muestreo por inmersin se realiza cuando se requiere tomar muestras de profundidad, por ejemplo en el caso de muestreos de tanques de almacenamiento que no cuenten con dispositivos de muestras o en el agua de manantiales o pozos, piscinas. En estos casos la muestra se debe tomar con un recipiente estril en su interior y exterior. Tan pronto se va a tomar la muestra, se debe retirar la envoltura protectora del recipiente y con la ayuda de una vara de muestreo se lleva a cabo la toma de muestra. En el caso de toma de las piscinas, la muestra se debe tomar de -10 a -20 cm de profundidad y el recipiente se introduce horizontalmente para evitar la prdida de tiosulfato. Para balnearios se hace de manera similar haciendo la toma a -20 a -30. En el caso de pozos o manantiales sin dispositivos de muestreo, la toma se debe hacer con bombas o dispositivos sumergibles estriles.
      .row(titulo="Muestreo tomado al azar")
        .col-12
          p.mb-0 Se basa en una muestra que se toma totalmente al azar, por medio de un recipiente abierto, el cual se coloca en un solo punto en la superficie (arroyo, embalse, rio, etc.); estas muestras por lo general se toman antes de hacer otro trabajo en el lugar para no afectar la corriente y la posterior recoleccin; para este ejercicio se utilizan muestreadores colectores que suelen pararse corriente abajo de la botella durante la operacin de llenado.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Procedimiento para el muestreo de agua
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0
        p El procedimiento para el muestreo de agua integra varios elementos y pasos, entre los que se destacan la identificacin del sito de la toma de muestra y la informacin requerida. La identificacin del sitio debe hacerse de manera unvoca. Si se dispone de GPS posicionar satelitalmente la ubicacin, de lo contrario especificar el lugar de la manera ms concreta posible.
        p.mb-0 En cuanto a la informacin requerida, en el momento del muestreo es necesario recabar, como mnimo:
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img17.png', style="width: 470px"  alt="").m-auto
    .row.mb-5
      .col-12.bg-c7.p-4
        ul.lista-ul--color.lista-ul.mb-0
          li
            i.fas.fa-check
            span #[b Identificacin unvoca de la muestra]
          li
            i.fas.fa-check
            span #[b Identificacin del sitio de muestreo (georreferenciacin)]
          li
            i.fas.fa-check
            span #[b Tipo de fuente y caractersticas de la misma (cercana a pozos negros o industrias, profundidad del nivel esttico y total, si fuera pozo o perforacin, existencia de pozos abandonados, etc.)]
          li
            i.fas.fa-check
            span #[b Destino (consumo humano, animal, riego, etc.)]
          li
            i.fas.fa-check
            span #[b Informacin acerca del Establecimiento donde se ha muestreado (ubicacin, propietario, vas de contacto, etc.)]
          li
            i.fas.fa-check
            span #[b Condiciones de muestreo (fecha y hora, nombre de quien realiz el muestreo)]
          li
            i.fas.fa-check
            span #[b Tipo de anlisis a efectuar]
          li
            i.fas.fa-check
            span #[b Reactivo empleado para su preservacin, en caso de ser utilizado]
          li.mb-0
            i.fas.fa-check
            span #[b Cualquier otra observacin que se considere de importancia]
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Precauciones para la toma de la muestra en funcin de su origen
    p Las muestras de agua pueden provenir de fuentes superficiales (ros, arroyos, canales, represas, lagos) o subterrneas (pozos) y este aspecto definir las condiciones de muestreo.
    p.mb-5 En funcin de la fuente que se vaya a muestrear, y para asegurar que la muestra sea lo ms representativa posible del total, se tendrn en cuenta las siguientes consideraciones:
    .tarjeta.tarjeta--azul.p-4.mb-5
      SlyderA(tipo="b")
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Agua de red
            p Se abrir el grifo o canilla y se dejar que el agua corra por lo menos durante 5 minutos de manera de tener purgada toda la caera que llega desde el tanque. El ramal donde se encuentre el grifo debe ser el principal, proveniente de la red, y no debe estar conectado en el trayecto con otras caeras, filtros, ablandadores u otros artefactos que puedan alterar la calidad del agua del ramal principal. Tambin se debe tomar la precaucin de retirar del grifo o boca de salida las mangueras u otros accesorios, y de limpiarlo tratando de eliminar sustancias acumuladas en el orificio interno de salida del agua y en el reborde externo, dejando correr agua libremente por espacio de 1 o 2 minutos, para arrastrar cualquier residuo.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img18.png', style="width: 500px" alt="").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Agua de perforaciones
            p La muestra se debe tomar de la caera inmediata al pozo y es conveniente que, antes de proceder a la toma de la muestra, la impulsin se mantenga en marcha durante unos 10-15 minutos, hasta que el agua emerja clara (sin sedimentos ni restos vegetales) y que sea del acufero. Se debe prestar especial atencin a esto si el pozo estuviera en desuso. Si el pozo fuera nuevo se debe bombear el tiempo suficiente hasta que salga limpia, de manera de muestrear el agua del acufero y en lo posible bombear con caudal de diseo.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img19.png', style="width: 500px" alt="").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Agua superficial proveniente de un curso de agua (en movimiento)
            p Debe ponerse especial atencin en buscar puntos estratgicos de muestreo (puentes, alcantarillas, botes, muelles), ya que se debe muestrear de sitios donde el agua se encuentre en circulacin. Nunca muestrear desde donde se encuentra estancada. Si se tratara de muestreos peridicos o de control debe tratar de extraerse la muestra siempre en el mismo lugar cuando no es posible tomar la muestra directamente con la mano, debe atarse al frasco un sobre peso usando el extremo de un cordel limpio o en su caso equipo muestreador comercial.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img20.png', style="width: 500px" alt="").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Agua superficial proveniente de un espejo de agua, represa o tanque de almacenamiento
            p En estos casos, se puede proyectar una jabalina a unos 2 metros de la orilla, para no muestrear del borde, evitando tomar la muestra de la capa superficial o del fondo. Sumergir el frasco en el agua (incorporando un peso) con el cuello hacia abajo hasta una profundidad de 15 a 30 cm., destapar y girar el frasco ligeramente permitiendo el llenado. Retirar el frasco despus que no se observe ascenso de burbujas.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img21.png', style="width: 500px" alt="").m-auto
        .row.m-1
          .col-lg-6.mb-4.mb-lg-0
            h4 Pozo somero o fuente similar (cisterna, aljibe, etc.)
            p Tomar la muestra bajando el frasco dentro del pozo hasta una profundidad de 15 a 30 cm. desde la superficie libre del lquido, evitando en todo momento tocar las paredes del pozo. Cuando no es posible tomar la muestra directamente con la mano, debe atarse al frasco un sobre peso usando el extremo de un cordel limpio o en su caso equipo muestreador comercial.
          .col-lg-6
            figure.mb-0
              img(src='@/assets/curso/tema1/img22.png', style="width: 500px" alt="").m-auto
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Acondicionado y preservacin de las muestras en el almacenamiento temporal.
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0
        p El acondicionamiento de las muestras depender del objetivo del muestreo. En general, para anlisis fsico-qumico puede ser necesario acondicionarlas con conservadores de fro, ya que algunas especies qumicas (nitratos, sulfatos) pueden sufrir transformaciones por accin microbiana. Tambin deben mantenerse al resguardo de la luz, procurando enviarlas lo ms rpido posible al laboratorio.
        .cajon.color-acento-contenido.p-4
          p.mb-0 En el caso de anlisis microbiolgico es indispensable que la muestra se mantenga refrigerada hasta su arribo al laboratorio, ya que tanto las temperaturas mayores a 6 C como la luz provocan la multiplicacin de los microorganismos e invalidan la muestra dado que los resultados no reflejarn la realidad.
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img23.png', style="width: 470px" alt="").m-auto
    .row.justify-content-center.mb-5
      .col-lg-10
        .row.bg-c1
          .col-12.px-4.px-lg-2.pt-4.pt-lg-2.pb-4.pb-lg-0
            .row.align-items-center
              .col-12.col-lg-auto.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img1.svg', style="width: 80px" alt="").m-auto
              .col
                p.mb-0 Siempre es conveniente tomar la muestra y transportarla los primeros das de la semana, previendo feriados o das no laborables, ya que, si se requiriera anlisis microbiolgico, una vez en el Laboratorio son necesarias por lo menos 48 horas, para realizar los cultivos.
    .row.justify-content-center
      .col-lg-10
        .row.bg-c1
          .col-12.px-4.px-lg-2.pt-4.pt-lg-2.pb-4.pb-lg-0
            .row.align-items-center
              .col-12.col-lg-auto.mb-4.mb-lg-0
                figure
                  img(src='@/assets/curso/tema1/img1.svg', style="width: 80px" alt="").m-auto
              .col
                p.mb-0 En cualquier caso, debe evitarse el congelamiento de la muestra (el lugar correcto para conservar las muestras que no se hayan podido entregar al laboratorio es en la parte inferior de una heladera comn).
    Separador
    #t_1_5
    .titulo-segundo.color-secundario
      h2 1.5 Mtodos de aforo para determinacin de caudales
    p El aforo de caudal es la cantidad de un fluido que atraviesa una seccin en un determinado tiempo. Es conocido como la tasa de flujo. En particular, flujo volumtrico (Q) es el volumen de un fluido que circula en una seccin por unidad de tiempo, y sus unidades son el m3/s.
    p Para la medicin del caudal es posible aplicar diferentes mtodos que pueden ser seleccionados en funcin de las caractersticas del flujo, tecnologa disponible, y exactitud de los datos requerida.
    .row.mb-5
      .col-lg-5.mb-4.mb-lg-0
        p Para la medicin del caudal en una corriente se han desarrollado diversos mtodos de aforo que se aplican segn el tamao del cauce, la magnitud del caudal, las caractersticas hidrulicas del flujo, la necesidad de contar con datos inmediatos o a corto plazo y, en general, las dificultades para realizar el aforo, entre otros. Entre los mtodos ms usados se encuentran:
      .col-lg-7
        .row
          .col-12.bg-c7.p-4
            ul.lista-ul--color.lista-ul.mb-0
              li
                i.fas.fa-check
                span #[b Mtodo rea-Velocidad].
              li
                i.fas.fa-check
                span #[b Mtodo Volumtrico].
              li
                i.fas.fa-check
                span #[b Mtodo con Trazadores (dilucin)].
              li
                i.fas.fa-check
                span #[b Estructuras Aforadas].
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Mtodos rea velocidad
    .row.align-items-center.mb-4
      .col-lg-7.mb-4.mb-lg-0
        p Dado que el caudal es funcin del rea de la seccin ocupada por el agua y la velocidad media del flujo, este procedimiento se basa en la determinacin de estas variables. Este sistema de aforo es el de mayor uso y requiere que el flujo tenga un comportamiento laminar y que las lneas de flujo sean normales a la seccin transversal de aforo.
        p.mb-4 El caudal en una corriente de agua es funcin del rea de la seccin de aforos y de la velocidad media del flujo y se obtiene mediante el producto de estas dos variables:

        p.my-5.text-center.h2
          span.bg-c3.py-2.px-4 #[strong #[em Q = A#[sub v]]]

        p Si la geometra del perfil de la seccin de aforos no se modifica, la velocidad mantiene su comportamiento horizontal y en profundidad, por el contrario, si la geometra cambia se altera la relacin nivel - rea, en consecuencia, la velocidad cambia su comportamiento.
        p El mtodo de rea-velocidad se realiza con diferentes #[strong mtodos de aforo], as:
        .row
          .col-12.bg-c7.p-4
            ul.lista-ul--color.lista-ul.mb-0
              li
                i.fas.fa-check
                | #[b Molinete Hidromtrico (vadeo, suspensin, angular, bote cautivo, lancha en movimiento).]
              li
                i.fas.fa-check
                | #[b Aforo con Flotadores.]
              li
                i.fas.fa-check
                | #[b Aforo ADCP #[em (Acoustic Doppler Current Profiler).]]
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img24.png', style="width: 470px" alt="").m-auto

    p.mb-5 En relacin con Mtodos, rea y Velocidad, tenga en cuenta los siguientes aspectos:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--verde")
      .row(titulo="Medicin de caudales")
        .col-12
          p.mb-0 La medicin de caudales a travs del mtodo rea-velocidad, se determina a travs de la seleccin de nmero de vrtices, determinacin del ancho, determinacin de la profundidad, clculo del rea, medicin de la velocidad, clculo del rea y caudal.
      .row(titulo="Profundidad del flujo")
        .col-12
          p.mb-0 La profundidad del flujo en la seccin transversal se mide en las verticales mediante una varilla o hilo de sondeo. Al tiempo que se mide la profundidad, se efectan observaciones de la velocidad mediante un molinete en uno o ms puntos de la vertical. Las anchuras, profundidades y velocidades medidas permiten calcular el caudal para cada segmento de la seccin transversal. La suma de estos caudales parciales ser el caudal total.
      .row(titulo="Molinete hidromtrico")
        .col-12
          p.mb-0 Teniendo en cuenta las diferentes alternativas de medir el caudal, se determina el tipo de aforo con molinete hidromtrico (vadeo, suspensin, angular, bote cautivo, lancha en movimiento) que mejor se ajuste a las condiciones existentes, en el momento de la campaa de monitoreo, especficamente la magnitud de la profundidad, el ancho, la velocidad, la disposicin de estructuras de apoyo como puentes o tarabitas y el tipo de rgimen de caudales predominante.
      .row(titulo="Aforo por vadeo")
        .col-12
          p.mb-0 Se utiliza cuando la profundidad es menor de un metro (< 1 m) y la velocidad de la corriente menor de un metro por segundo (< 1 m/s). Estas condiciones permiten que los operarios y los equipos se metan al cauce con seguridad, garantizando de esta manera que la medicin se realice con comodidad y sin riesgo.
      .row(titulo="Aforo por suspensin")
        .col-12
          p.mb-0 Cuando las condiciones del flujo (profundidad y/o velocidad) presentan amenaza para los operarios y equipos, es necesario realizar las mediciones desde un puente o una tarabita. Aqu los equipos van suspendidos desde un malacate o torno a travs de un cable coaxial, que adicionalmente sirve para medir la profundidad en las diferentes abscisas de medicin.
      .row(titulo="Aforo en bote cautivo")
        .col-12
          p.mb-0 Aplicable en ros o canales medianos, donde es posible tender una manila o cable de orilla a orilla, que sirve de apoyo a la embarcacin para contrarrestar el empuje de la corriente.
      .row(titulo="Aforo con flotadores")
        .col-12
          p.mb-0 Se utiliza cuando se requiere medir en forma rpida el caudal en una corriente que presenta una lmina de pocos centmetros de profundidad, cuando se est en presencia de grandes cantidades de material en suspensin, o cuando deba efectuarse una medicin del caudal en un perodo muy breve, para lo cual se mide la velocidad superficial a lo ancho del cauce utilizando flotadores especialmente diseados y suministrados para este efecto.
      .row(titulo="Aforo ADCP <i>(Acoustic Doppler Current Profiler)</i>")
        .col-12
          p.mb-0 Se utiliza para medir el caudal en ros grandes o pequeos, sin rocas grandes que permita el desplazamiento horizontal del equipo para medir el caudal.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Mtodo Volumtrico
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0
        p Cuando se trate de medir caudales pequeos en condiciones que no permitan el uso del molinete, o no se cuente con este equipo, se utiliza el aforo volumtrico, que consiste sencillamente en recolectar en un recipiente previamente calibrado, un volumen de agua conocido y tomar con precisin el tiempo de recoleccin, preferiblemente con cronmetro.
        p Se recomienda utilizarlo en corrientes pequeas, en las cuales se pueda colectar en un recipiente calibrado el 100% del flujo a medir. La calibracin del recipiente y el tiempo de recoleccin deben ser muy precisos. Se puede recurrir a recipientes de uso comn como un balde o caneca que tenga registros de volumen; en otros casos el aforo se realiza en tanques de mayor tamao que tengan dimensiones precisas, de tal manera que mediante la medicin de un diferencial de nivel se determina un incremento de volumen y tomando el tiempo de incremento de volumen se puede calcular directamente el caudal que lleva la corriente o el canal.
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img25.png', style="width: 470px" alt="").m-auto
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Mtodo con Trazadores (dilucin)
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0
        p La medicin del caudal mediante este mtodo est basada en la determinacin del grado de dilucin en el agua de la corriente de una solucin del trazador vertida en ella.
        p Este mtodo se recomienda utilizar para secciones de aforo donde se encuentren:
        .row
          .col-12.bg-c7.p-4
            ul.lista-ul--color.lista-ul.mb-0
              li
                i.fas.fa-check
                span #[b Grandes turbulencias y remolinos]
              li
                i.fas.fa-check
                span #[b Rgimen torrencial]
              li
                i.fas.fa-check
                span #[b Altas pendientes]
              li
                i.fas.fa-check
                span #[b Poca profundidad]
              li
                i.fas.fa-check
                span #[b Lechos inestables]
              li.mb-0
                i.fas.fa-check
                span #[b Lneas de flujo desordenadas]
      .col-lg-5
        figure
          img(src='@/assets/curso/tema1/img26.png', style="width: 470px" alt="").m-auto
    p.mb-5 Sobre los aforos con trazadores, tambin llamados aforos qumicos, tenga presente:
    .row.justify-content-center.mb-5
      .col-11.col-md-5.col-lg.bg-c10.p-4.m-2
        p.text-center.mb-4 #[b Variacin de concentracin]
        p.text-center.mb-0 Los aforos con trazadores tambin llamados aforos qumicos, permiten conocer el caudal a partir de la variacin de concentracin de una sustancia inyectada en el cauce que permite estudiar su comportamiento y evolucin.
      .col-11.col-md-5.col-lg.bg-c10.p-4.m-2
        p.text-center.mb-4 #[b Conductividad elctrica]
        p.text-center.mb-0 El procedimiento consiste en inyectar un trazador en una seccin de la corriente y realizar aguas abajo, a una distancia lo suficientemente lejos para que haya dilucin total, mediciones de conductividad elctrica para detectar el paso de la nube y as calcular el caudal.
      .col-11.col-md-5.col-lg.bg-c10.p-4.m-2
        p.text-center.mb-4 #[b Caractersticas necesarias de los trazadores]
        p.text-center.mb-0 Los trazadores deben cumplir con una serie de caractersticas como lo son la solubilidad, posibilidad de medicin #[em in situ], costo reducido, no contaminante, entre otros.
      .col-11.col-md-5.col-lg.bg-c10.p-4.m-2
        p.text-center.mb-4 #[b Condiciones de uso de los trazadores]
        p.text-center.mb-0 El trazador empleado para la aplicacin del mtodo no debe estar presente en el cuerpo de agua en cantidades apreciables. Algunos de los ms empleados en la prctica son el cloruro de sodio y el dicromato de sodio.
    .row.mb-4
      .col-auto.txt-c1.px-4.py-3
        h4.text-white.mb-0 Estructuras Aforadas
    p Son estructuras que han sido estudiadas y calibradas en diferentes condiciones experimentales. Para cada una de ellas es posible obtener una ecuacin de descarga (relacin Nivel-Caudal) que permite determinar el caudal instantneo en funcin de la altura de la lmina de agua con respecto a un punto de la estructura, que se mide con ayuda de una mira o un instrumento registrador.
    p.mb-5 A continuacin, se presentan las estructuras ms utilizadas:
    .row.justify-content-center
      .col-lg-10
        .titulo-sexto.color-acento-contenido
          p.mb-0 #[b Tabla 5.] Estructuras aforadas de mayor uso
        .tabla-b.color-acento-contenido
          table
            tr.bg-c2
