                  img(src='@/assets/curso/tema1/img27.png', style="width: 420px" alt="").m-auto
                p.mb-0 Son dispositivos hidrulicos fijos o removibles que consisten en una escotadura a travs de la cual se hace circular el flujo que se quiere medir en el canal o corriente natural. La precisin del aforo depende de la velocidad de llegada a la estructura, por lo tanto, es importante remansar el agua ampliando la seccin del canal arriba del sitio para obtener velocidades mnimas (< 0,15 m/s).
              td
                figure.mb-4
                  img(src='@/assets/curso/tema1/img28.png', style="width: 420px" alt="").m-auto
                p.mb-0 Son estructuras de gran aplicacin en terrenos planos ya que funcionan a flujo libre con prdidas de carga pequeas. Las canaletas ms utilizadas son: tipo #[em Balloffet] (tiene paredes paralelas y fondo plano), medidor sin cuello o #[em Cutthroat] (consiste en una seccin de entra, una de salida, una garganta y un fondo aforador) y, finalmente, canaleta Parshall (conformada por una seccin convergente de contraccin que se localiza en su extremo aguas arriba, una garganta y una seccin divergente aguas abajo).
